* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  margin: 1rem;
}

.page {
  padding: 0 1.25rem;
}

.products-display {
  max-width: 1400px;
  margin: 2rem auto;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
  gap: 2rem;
}

section.centered {
  padding: 4rem 0;
  max-width: 420px;
  margin: auto;
  text-align: center;
  line-height: 150%;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}

button,
.button {
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  display: block;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button.main,
.button.main {
  color: white;
  background-color: black;
  padding: 12px 24px;
}

button.main:hover,
.button.main:hover {
  opacity: 0.7;
}

.regular {
  font-weight: 400;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}
